@import url("https://fonts.googleapis.com/css2?family=Oswald&display=swap");

body {
  position: relative;
  background: radial-gradient(rgb(24, 206, 0) 0%, rgb(0, 0, 0) 49.9%, rgb(30, 255, 0) 50.1%, rgb(0, 0, 0) 58%);
  font-family: "Oswald", sans-serif;
  background-size: 120% 200%;
  background-position: -20% 50%;
  background-attachment: fixed;
  animation: 20s backgroundBody infinite ease;
}

@keyframes backgroundBody {
  0% {
    background-position: 30% 50%;
  }
  50% {
    background-position: 70% 50%;
  }
  100% {
    background-position: 30% 50%;
  }
}

html {
  scroll-behavior: smooth;
}

.navbar {
  position: fixed;
  height: 8vh;
  width: 100%;
  background-color: rgba(167, 0, 201, 0);
  z-index: 200;
  transition: 0.5s;
  filter: opacity(0.5);
}

.navbar:hover {
  background-color: rgb(50, 175, 0);
  filter: opacity(1);
}

.navbar__items {
  width: 50%;
  height: 100%;
  display: flex;
  margin: auto;
  justify-content: space-between;
}

.navbar__link {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  font-size: 3vmin;
  color: rgb(255, 255, 255);
}

.navbar__link:hover {
  color: rgb(182, 182, 182);
  cursor: pointer;
}

.scroll:hover {
  filter: opacity(1);
}

#container-welcome,
#container-projects,
#container-skills,
#container-social {
  position: relative;
  height: 100vh;
}

.bottom-bar {
  position: relative;
  bottom: 0;
  height: fit-content;
  background-color: rgb(0, 0, 0);
  padding-bottom: 2vmin;
  z-index: 1000;
}

.name-wrapper {
  position: relative;
  top: 28vh;
  margin: auto;
  width: 44%;
  z-index: 2;
  transition: 0.2s;
  text-align: center;
  color: rgb(255, 255, 255);
}

@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");

.name-wrapper__large {
  position: relative;
  font-size: 10vmin;
  font-family: "Lobster", cursive;
  margin-top: 3vmin;
  margin-bottom: 3vmin;
  top: 8vmin;
  color: rgb(255, 255, 255);
  border-radius: 3vmin;
  text-shadow: 0px 0px 15px rgb(9, 255, 0), 0px 0px 35px rgb(9, 255, 0);
  animation: 2s;
  transition: 2s;
  cursor: pointer;
  transform: translate(0%);
}

.name-wrapper__medium {
  position: relative;
  font-family: "Lobster", cursive;
  font-size: 5vmin;
  font-weight: 100;
  letter-spacing: 0.1vw;
  margin: 0;
  padding: 0;
  top: 8vmin;
  filter: opacity(1);
  z-index: 100;
  transition: 2s;
  transform: translate(-20%);
}

.name-wrapper__small {
  position: relative;
  font-weight: 400;
  font-size: 3vmin;
  letter-spacing: 0.1vmin;
  top: 8vmin;
  filter: opacity(1);
  z-index: 100;
  transition: 2s;
  transform: translate(0%);
}

a:hover,
a:visited,
a:link,
a:active {
  text-decoration: none;
}

.section__header {
  position: relative;
  font-size: 6vmin;
  color: rgb(255, 255, 255);
  text-shadow: 1px 1px 1px rgb(0, 0, 0);
  font-weight: 500;
  text-align: center;
  margin: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  padding-top: 12vmin;
  margin-bottom: 5vmin;
  z-index: 100;
}

.black {
  color: black;
}

.bottom-bar__text-item {
  color: white;
  position: relative;
  text-align: center;
  font-size: 1vmax;
  font-weight: 400;
  letter-spacing: 0.2vmin;
}

.container-projects__project-wrapper {
  position: relative;
  width: 45%;
  margin-top: 5%;
  margin-left: 10%;
  transition: 1s;
  border-radius: 2vmin;
  filter: drop-shadow(0px 0px 10px rgb(132, 0, 255)) drop-shadow(0px 0px 30px rgb(132, 0, 255)) drop-shadow(0px 0px 80px rgb(132, 0, 255));
  transform: translateY(50%);
  transition: 1s;
}

.skills {
  transition: 1s;
}

.container-skills__skill-wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 80%;
  margin: auto;
  padding: 5vmin;
  border-radius: 3vmin;
  transition: 1s;
}

.container-skills__skill-item {
  position: relative;
  display: inline-grid;
  width: 9vmin;
  height: 9vmin;
  text-align: center;
  font-weight: 900;
  color: rgb(0, 0, 0);
  margin: 1vmin;
  overflow: hidden;
  background-color: rgba(0, 247, 255, 0.1);
  filter: drop-shadow(0px 0px 10px rgb(0, 206, 233));
  animation: 1s;
  border-radius: 100vmin;
}

.container-skills__skill-item:nth-child(2n) {
  transform: translateY(20%);
}

.container-skills__skill-item:hover {
  background-color: rgba(0, 247, 255, 0.2);
}

.container-skills__image {
  width: 100%;
  display: block;
  margin: auto;
}

.bottom-bar__contact-wrapper {
  width: 50%;
  margin: auto;
  padding: 5vmin;
  padding-top: 2vmin;
  padding-bottom: 2vmin;
  display: flex;
  border-radius: 3vmin;
}

.bottom-bar__contact-item {
  display: block;
  margin: auto;
  margin-top: 3vmin;
  margin-bottom: 3vmin;
}

.bottom-bar__contact-item:hover {
  filter: brightness(1.2);
}

.bottom-bar__image {
  width: 30%;
  display: block;
  margin: auto;
  border-radius: 0%;
  background-color: black;
  border-radius: 2vmin;
}

.navbar__icon {
  display: none;
}

.container-scroll {
  position: fixed;
  display: flex;
  justify-content: center;
  width: 100%;
  top: 88%;
  z-index: 1000;
}

.container-scroll__button {
  width: 4vmin;
  margin-left: 1vmin;
  margin-right: 1vmin;
  transition: 0.3s;
}

.container-scroll__button:hover {
  transform: scale(1.3);
}

.carousel-control-next,
.carousel-control-prev {
  height: 20%;
  width: 10%;
  top: 78%;
  background-color: rgba(18, 216, 0, 0.5);
  transition: 0.3s;
}

.carousel-control-next:hover,
.carousel-control-prev:hover {
  background-color: rgba(18, 216, 0, 0.8);
}

.carousel-inner {
  overflow: visible;
}

.carousel-caption {
  color: rgb(255, 255, 255) !important;
  background-color: black;
  border-radius: 3vmin;
}

.carousel-text {
  position: absolute;
  top: 0%;
  left: 102%;
  background-color: rgb(61, 61, 61);
  color: white !important;
  z-index: 100;
  width: 50vmin;
  padding: 2vmin;
}

.text-green {
  color: rgb(40, 228, 15) !important;
}

.carousel-link {
  color: rgb(28, 243, 0) !important;
}

.carousel-link:hover {
  color: rgb(101, 255, 101) !important;
}

.container-social_item {
  width: 80%;
  display: block;
  position: relative;
  border-radius: 3vmin;
  box-shadow: 0px 0px 30px rgb(0, 255, 0);
}

@media only screen and (max-device-width: 600px), (max-width: 600px) {
  .container-social_item {
    width: 100% !important;
    top: 0%;
    margin-bottom: 5vmin;
  }

  .carousel-inner {
    height: 60vh;
  }

  .carousel-caption {
    position: relative;
    width: 80% !important;
    top: 0;
    left: 0;
    right: 0;
  }

  .carousel-control-next,
  .carousel-control-prev {
    height: 20%;
    top: 22%;
  }

  body {
    background:none;
    background-color: rgb(30, 180, 0);
    background-attachment: scroll;
    overflow-x: hidden;
  }

  .name-wrapper {
    position: relative;
    display: block;
    width: 90%;
    z-index: 2;
    margin: auto;
    top: 40vmin;
  }

  .name-wrapper__large {
    font-size: 18vmin;
    width: 100%;
    margin-top: 5vmin;
    margin-bottom: 5vmin;
  }

  .name-wrapper__medium {
    font-size: 9vmin;
  transform: translate(0%);
  }

  .name-wrapper__small {
    font-size: 5.5vmin;
  }

  .section__header {
    font-size: 12vmin;
    padding-top: 10vmin;
    width: 90%;
  }

  #container-welcome,
  #container-projects,
  #container-skills,
  #container-social {
    position: relative;
    height: 100vh;
  }

  .container-skills__skill-wrapper {
    padding-left: 2vmin;
    padding-right: 2vmin;
    width: 90%;
  }

  .container-skills__skill-item {
    width: 14%;
    height: unset;
  }

  .service-wrapper {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-between;
    margin-top: 0vh;
    padding-left: 0;
    padding-right: 0;
  }

  .bottom-bar__text-item {
    font-size: 4vmin;
  }

  .bottom-bar__contact-wrapper {
    width: 80%;
    padding: 2vmin;
    flex-wrap: wrap;
    margin-top: 0;
  }

  .navbar {
    position: fixed;
    width: 100%;
    height: 10vh;
    z-index: 5;
    transition: 1s;
    padding: 0;
  }

  .navbar__items {
    width: 100%;
    height: 60vh;
    display: flex;
    flex-direction: column;
    padding-left: 5vmin;
    justify-content: space-evenly;
    background-color: rgb(0, 0, 0);
    margin-left: 0%;
    top: 0%;
  }

  .navbar__link {
    position: relative;
    font-size: 8vmin;
    color: rgb(255, 255, 255);
  }

  .navbar__icon {
    position: absolute;
    height: 80%;
    top: 1vh;
    right: 5%;
    float: right;
    display: block;
  }

  .container-skills__image {
    display: block;
    margin: auto;
    border-radius: 0%;
  }

  .bottom-bar__contact-item {
    width: 15%;
    margin-top: 1vmin;
    margin-bottom: 5vmin;
  }

  .bottom-bar__image {
    width: 70%;
    display: block;
    margin: auto;
    border-radius: 0%;
    background-color: black;
    padding: 0;
    border-radius: 2vmin;
  }

  .bottom-bar {
    height: fit-content;
    background-color: rgb(0, 0, 0);
  }

  .container-scroll {
    display: none;
  }

  .container-projects__project-wrapper {
    position: relative;
    width: 85%;
    margin-top: 10%;
    margin-left: 7.5%;
    transition: 1s;
    border-radius: 2vmin;
    filter: drop-shadow(0px 0px 10px rgb(132, 0, 255)) drop-shadow(0px 0px 30px rgb(132, 0, 255)) drop-shadow(0px 0px 80px rgb(132, 0, 255));
    transform: translateY(50%);
    transition: 1s;
  }

  .carousel-text {
    position: absolute;
    top: 107%;
    left: 0%;
    background-color: rgb(61, 61, 61);
    color: white !important;
    width: 85vw;
    height: fit-content;
    padding: 2vmin;
    font-size: 0.9rem;
  }

  .wm-100 {
    width: 100% !important;
  }
}
